<template>
      <div class="container-fluid">
        <div class="row">
            <div class="col-md-6 col-sm-6 col-xs-12  portada">
                <img src="../assets/multigroup-logo.png" class="img-responsive logo-portada" alt="Responsive image">
            </div>
            <div class="col-md-6 col-sm-6 col-xs-12  login">
                <div class="col-md-2"></div>
                <div class="box-login col-md-8">
                    <img src="../assets/metalus-logo.png" alt="" class="logo-metalus-login"><br>
                    <div id="firebaseui-auth-container" lang="es"></div>
                    <p>Solo cuentas de multigroup.com y xsteel.co</p>
                    <p>-- O --</p>
                    <div class="input-group">
                        <span class="fas fa-user"></span>
                        <input type="text" placeholder="Usuario"><br>
                    </div>
                    <div class="input-group">
                        <span class="fas fa-lock"></span>
                        <input type="password" placeholder="Contraseña"><br>
                    </div>
                    <a href="#">
                      <button class="login-button">Login</button>
                    </a>
                    <div class="footer">
                        <p>MultiGroup 2019 <span class="far fa-registered"></span> V1.0.3</p>
                    </div>  
                </div> 
                <div class="col-md-2"></div> 
            </div>
        </div>
    </div>

</template>

<script>
  import auth from '@/auth'

  export default {
    name: 'auth',
    mounted() {
      auth.authForm('#firebaseui-auth-container')
    },
    methods:{
      sigN(){
        auth.sign()
      }
    }
  }
</script>

<style>
  @import "../../node_modules/firebaseui/dist/firebaseui.css";


  /* LOGIN */
  #firebaseui-auth-container{
    margin-top: 20px !important;
  }
  .box-login{
    color: #85898c;
    margin-top: 20%;
    text-align: center;
    padding: 0 10%;
  }
  .box-login input{
    border: 1px;
    border-radius: 10px;
    margin: 10px 0;
    outline: none;
    padding: 5px 20px;
    width: 100%;
  }

  .login-button{
    background: #85898c !important;
    margin: 10px 0 !important;
    width: 80% !important;
  }
  .google-button, .login-button{
    background: #c10d25;
    color: white;
    padding: 8px 20px;
    width: 100%;
    border-radius: 10px;
    border: none;
    margin: 50px 5px 0 0;
  }
  
  .google-button:focus, .login-button:focus{
    outline: none;
  }

  .login{
    background: #e5e2e2;
    height: 100vh;
  }

  .logo-metalus-login{
    width: 100%;
    margin: 0 auto;
  }

  .logo-portada{
    width: 350px;
    margin: 0 auto;
    margin-top: 50%;
  }

  .portada{
    display: none;
    /* background-image: url(img/planta-multi.jpg); */
    /* background-image: url(../assets/planta-multi.jpg); */
    background-position: center;
    background-size: cover;
    height: 100vh;
  }

  .input-group{
    position: relative;
    width: 100%;
  }

  .input-group input { text-indent: 32px;}

  .input-group .fa-user, .fa-lock { 
    position: absolute;
    top: 18px;
    left: 10px;
  }


  /* MEDIA QUERYS */

  @media (min-width: 480px) {
    .circle__content {
      font-size: 2em;
    }
  }
  
  @media (min-width: 768px) {
    html{
      overflow: hidden;
    }
    .navbar-controles span{
      display: initial;
      color: #fff;
      font-size: 2em;
    }
    .navbar-controles a,span{
      text-decoration: none;
    }
    .circle {
        position: relative;
        display: block;
        margin: 2em 0;
        background-color: transparent;
        color: #fff;
        text-align: center;
      }
    .circle__content {
      font-size: 1.5em;
    }
    .portada{
      display: initial;
      /* background-image: url(img/planta-multi.jpg); */
      background-position: center;
      background-size: cover;
      height: 100vh;
    }
    .footer {
      position: fixed;
      right: 0;
      bottom: 0;
      width: 50%;
      color: #85898c;
      text-align: center;
    }
  }

  
</style>



