<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-4 col-sm-4 col-xs-12"></div>
      <div class="col-md-4 col-sm-4 col-xs-12">
        <div>
          <div class="caution-login">
            <div class="caution">
              <i class="fas fa-exclamation-triangle"></i>
              <h3 class="display-4">Lo Sentimos!</h3>
            </div>
            <p class="caution-text">El dominio que se identifico no pertenece a la <strong>red de MultiGroup</strong> por lo cual no puede acceder, póngase en contacto con los administradores para mas información</p>
            <p class="lead">
              <a class="btn btn-default btn-lg" href=""  @click="logOut">Regresar al Login</a>
            </p>
          </div>
        </div>
      </div>
      <div class="col-md-4 col-sm-4 col-xs-12"></div>
    </div>
    <div class="footer-dashboard">
      <p>MultiGroup 2019 <span class="far fa-registered"></span>  V1.0.3</p>
    </div>  
  </div> 
</template>

<script>
    export default {
        name: 'home',
        methods: {
          logOut() {
          auth.logout()
      }
    }  
    }
</script>

<style>

.caution-login{
  background-color: yellow;
  border-radius: 15px;
  margin-top: 50px;
  padding: 20px;
}

.caution{
  text-align: center;
}
.caution i{
  font-size: 3em;
}
.lead{
  text-align: center;
}

.footer-dashboard {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 100%;
  color: #85898c;
  text-align: center;
}

.footer-dashboard{
  width: 100% !important;
}

</style>



