import firebase from 'firebase'
import firebaseui from 'firebaseui';

const config = {
  apiKey: "AIzaSyDJVNXuDcJtqQ1o1gJ2q39p020_SA69KNI",
  authDomain: "metalusapp.firebaseapp.com",
  databaseURL: "https://metalusapp.firebaseio.com",
  projectId: "metalusapp",
  storageBucket: "metalusapp.appspot.com",
  messagingSenderId: "928633720935"
};

const auth = {
  context: null,
  uiConfig: null,
  ui: null,
  
  init(context) {
    this.context = context;
    firebase.initializeApp(config);
    this.uiConfig = {
      signInFlow: 'popup',
      signInSuccessUrl: '#/load',
      signInOptions: [
        {
          provider: firebase.auth.GoogleAuthProvider.PROVIDER_ID,
          customParameters: {
            prompt: 'select_account'
          }
        },
      ]
  }
    
    this.ui = new firebaseui.auth.AuthUI(firebase.auth());
    firebase.auth().onAuthStateChanged((user) => {
     // document.getElementById('firebaseui-auth-container').style.display = 'none';
   
      this.context.$store.dispatch('user/setCurrentUser')
      let requireAuth = this.context.$route.matched.some(record => record.meta.requireAuth)
      let guestOnly = this.context.$route.matched.some(record => record.meta.guestOnly)

      if(requireAuth && !user) this.context.$router.push('/auth') 
      else if (guestOnly && user) this.context.$router.push('/load')
      var correo = user.email.substr(-14);
      var correox = user.email.substr(-9);
      if(correo != "multigroup.com" || correx !="xsteel.co"){
       this.context.$router.push('/home');
       firebase.auth().signOut();
      } 
      // else if (guestOnly && user) setTimeout( () => this.context.$router.push({ path: '/dashboard'}), 2000);
    });
  },
  authForm(container) {
    this.ui.start(container, this.uiConfig);
  },
  user() {
    return this.context ? firebase.auth().currentUser : null;
  },
  logout() {
    firebase.auth().signOut();
  }

  
}

export default auth;



